* {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}

body {
  background-image: url("./Assets/clouds.jpg");
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
  box-sizing: border-box;
  width: 100vw;
}

.All {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

@media screen and (max-width: 992px) {
  .All {
    height: 200vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
  }
}

.weatheroverview {
  max-width: 100vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.containerCard {
  width: 20vw;
  margin: 0 auto;
  overflow: auto;
  border: rgb(38, 57, 77);
  padding: 30px;
  border-radius: 5px;
  text-align: center;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  background-color: transparent;
  height: 65vh;
  font-size: 100%;
}

 @media screen and (max-width: 640px) { 
  .containerCard {
   height: 100%;
  }
}

@media screen and (orientation: landscape) {
  .All {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    flex-wrap: nowrap;
    height: auto;
    }
    .containerCard {
      max-height: 200vh;
      height: 100%;
    }
}

@media screen and (max-width: 992px) {
  .containerCard {
    width: 40vw;
    margin-bottom: 10%;
  }
}

@media screen and (max-width: 480px) {
  .containerCard {
    width: 70vw;
  }
}

.weatherOnly {
  border-radius: 10px;
  margin: 0 auto;
  background-color: rgba(233, 232, 232, 0.1);
  padding: 5% 0;
  min-width: 85%;
  margin-top: 4vw;
}

.button {
  width: 6vw;
  height: 6vw;
  border-radius: 50%;
  color: black;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  border: rgb(38, 57, 77);
  font-size: 1.5vw;
  background-color: transparent;
  cursor: pointer;
  text-align: center;
}

@media screen and (max-width: 992px) {
  .button {
    width: 10vw;
    height: 10vw;
    margin-bottom: 10%;
    font-size: 2.2vw;
  }
}

@media screen and (max-width: 480px) {
  .button {
    font-size: 4vw;
    width: 25vw;
    height: 25vw;
    margin-bottom: 40%;
  }
}

.buttonContainer {
  margin: 0 auto;
  width: auto;
  height: 100vh;
  padding: 2%;
  border-radius: 5px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

@media screen and (max-width: 992px) {
  .buttonContainer {
    display: flex;
    flex-direction: row;
    height: auto;
    width: 40vw;
  }
}

@media screen and (max-width: 480px) {
  .buttonContainer{
    width: 80vw;
    margin-bottom: 10%;
  }
}

.citybutton {
  width: 10vw;
  height: 10vw;
  border-radius: 50%;
  color: black;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  border: rgb(38, 57, 77);
  font-size: 1.5vw;
  background-color: transparent;
  cursor: pointer;
  text-align: center;
}

@media screen and (max-width: 992px) {
  .citybutton {
    font-size: 2vw;
    width: 15vw;
    height: 15vw;
  }
}

@media screen and (max-width:480px ) {
  .citybutton {
    font-size: 4vw;
    width: 25vw;
    height: 25vw;
  }
}

