.containerForecast {
  width: 20vw;
  margin: 0 auto;
  height: 65vh;
  max-height: 50%;
  border: rgb(38, 57, 77);
  padding: 30px;
  border-radius: 5px;
  text-align: center;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  background-color: transparent;
  overflow: hidden;
}

@media screen and (max-width: 640px) and (orientation: landscape) {
  .containerForecast {
    max-height: 200vh;
    height: auto;
    margin-top: 3%;
  }
}

@media screen and (max-width: 992px) {
  .containerForecast {
    width: 40vw;
  }
}

@media screen and (max-width: 480px) {
  .containerForecast {
    width: 70vw;
  }
}

.accordion {
  width: 100%;
  height: 65vh;
  background-color: transparent;
  margin-bottom: 1vw;
  border-radius: 5px;
  border: 1px solid black;
  overflow-y: scroll;
}

@media screen and (max-width: 640px) and (orientation: landscape) {
  .accordion {
    height: 100%;
  }
}

.accordion::-webkit-scrollbar {
  width: 15px;
}

.accordion::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(233, 232, 232);
  border-radius: 5px;
}

.accordion::-webkit-scrollbar-thumb {
  background: transparent;
  border-radius: 5px;
}

.accordion::-webkit-scrollbar-thumb:hover {
  background: transparent;
  box-shadow: inset 0 0 5px rgb(233, 232, 232);
}

.titleIcons {
  display: flex;
  justify-content: space-between;
  margin: 0 15px;
  margin-top: 2.3em;
  cursor: pointer;
  font-size: 100%;
  height: 100%;
}

.content {
  padding: 1.45%;
  background-color: rgb(71, 140, 180);
  font-size: 70%;
  word-spacing: 10px;
  letter-spacing: 0.3px;
}
